.ChartHolder {
    width: 640px;
    height: 480px;
    border-radius: 10px;
    background-color: #61dafb;
    color: white;
    font-weight: bolder;
    text-align: center;
    vertical-align: center;
    font-size: large;
    z-index: inherit;
}

.button {

    border-radius: 6px;
    height: 30px;
    background: teal;
    color: white;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
}