
.container-center {
    alignment: center;
    vertical-align: center;
    margin:20px;
}

.container {
    display:flex;
    flex-wrap: wrap;
    margin-Left: 8px;
    margin-Right: 8px;
    margin-top: 8px;
    margin-bottom: 8px;
    flex-Direction: row;
    justify-Content: flex-start;
    padding: 20px;
}



.button {
    padding: 0 20px;
    border-radius: 4px;
    height: 60px;
    background: teal;
    color: white;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}

hr {
    border-color: #e5e5e5;
}
