.sensor-item {
    box-sizing: border-box;
    border-radius: 10px;
    flex-basis: 32%;
    padding: 20px;
    text-decoration: none;
    display: block;
    background-color: darkcyan;
    color: white;
    margin-Left: 8px;
    margin-Right: 8px;
    margin-top: 8px;
    margin-bottom: 8px;

}

.sensor-item-red {
    box-sizing:border-box;
    border-radius: 10px;
    flex-basis: 32%;
    padding: 20px;
    text-decoration: none;
    display: block;
    background-color: coral;
    color: white;
    margin-Left: 8px;
    margin-Right: 8px;
    margin-top: 8px;
    margin-bottom: 8px;
}

.sensor-item img {
    max-width: 100%;
}

.sensor-item__title {
    margin: 10px 0;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
}

.button {

    border-radius: 4px;
    height: 60px;
    background: teal;
    color: white;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}
