.header {
    width: 100%;
    height: 40px;
    padding-top: 10px;
    background-color: darkcyan;
    color: white;
    font-weight: bolder;
    text-align: center;
    font-size: large;
    border-radius: 10px;
}

.header .container {
    height: 100%;
    display: flex;
    align-items: center;
    alignment: center;
}

.header-brand {
    font-size: 24px;
    font-weight: bold;
    color: inherit;
    text-decoration: none;
}
